import React from 'react';
import styled from 'styled-components';
import image from '../images/3.png';

const SectionWrapper = styled.div`
  width: 100vw;
  height: 80vh;
  background-image: url(${image});
  background-size: cover;

  h1 {
    font-size: 4vh;
    margin: 0;
    padding-left: 5vw;
    padding-top: 5vh;
  }
  h2 {
    margin: 0;
    font-size: 2.5vh;
    padding-left: 5vw;
    padding-top: 5vh;
    max-width: 40vh;
  }

  @media screen and (orientation: landscape) {
    h1 {
      font-size: 4vw;
      margin: 0;
      padding-left: 6vh;
      padding-top: 5vh;
    }
    h2 {
      margin: 0;
      font-size: 2vw;
      padding-left: 6vh;
      padding-top: 5vh;
      max-width: 50vw;
    }
  }
`;

const Section2 = () => {
  return (
    <SectionWrapper>
      <h1>Najważniejsze Zapisy</h1>
      <h2>
        W pierwszych zdaniach dokumentu podkreślono jedność państwa, czego
        wyrazem miał być jeden rząd, skarb i armia Artykuł pierwszy potwierdzał
        dominującą rolę religii katolickiej W drugim zaakcentowano pozycje
        szlachty, gwarantując jej przyznane dawniej przywileje
      </h2>
    </SectionWrapper>
  );
};

export default Section2;
