import React from 'react';
import Home from './views/Home';
import Seo from './components/Seo';
import styled from 'styled-components';
import Section1 from './views/Section1';
import Section2 from './views/Section2';
import Section3 from './views/Section3';
import OtherSection from './views/OtherSection';
import OtherSection2 from './views/OtherSection2';
import OtherSection3 from './views/OtherSection3';
import { scroller } from 'react-scroll';

const Wrapper = styled.div`
  width: 100vw;
  overflow-x: hidden;
`;

class App extends React.Component {
  state = {
    start: false,
  };
  startFunction = async () => {
    await this.setState({ start: true });
    setTimeout(() => {
      scroller.scrollTo('section1', {
        duration: 500,
        delay: 100,
        smooth: 'easeInOutQuart',
      });
    }, 50);
  };
  render() {
    return (
      <div>
        <Home startFunc={this.startFunction} />
        {this.state.start === true && (
          <>
            <Section1 id='section1' name='section1' />
            <OtherSection />
            <Section2 />
            <OtherSection2 />
            <Section3 />
            <OtherSection3 />
          </>
        )}
      </div>
    );
  }
}

export default App;
