import React from 'react';
import styled from 'styled-components';
import { Link, animateScroll as scroll } from 'react-scroll';
import k3m from '../images/k3m.jpg';
import img1 from '../images/2.png';
import Section1 from './Section1';
import { gsap } from 'gsap';

const HomeWrapper = styled.div`
  width: 100vw;
  height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(${k3m});
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden;

  h1 {
    opacity: 0;
    text-align: center;
    font-size: 6.5vh;
    color: white;
    margin: 0;
    display: block;
    align-self: center;
    transform: translateY(30vh) translateX(-2vw);
  }
  button {
    opacity: 0;
    display: block;
    align-self: center;
    justify-self: center;
    transform: translateY(45vh) translateX(-2vw);
    margin: auto auto;
    width: 35vh;
    height: 10vh;
    font-size: 4vh;
    border: none;
    background-color: #d14343;
    color: white;
    border-bottom: solid #8c3737 1vh;
    outline: none;
    cursor: pointer;
    :focus {
      border: none;
    }
  }
`;

const Home = ({ startFunc }) => {
  const wrapper = React.useRef(null);
  React.useEffect(() => {
    const tl = gsap.timeline();
    const h1 = wrapper.current.children[0];
    const btn = wrapper.current.children[1];

    tl.set([h1, btn], { autoAlpha: 0 })
      .fromTo(h1,0.65,{ autoAlpha : 0, x : 50 } , { autoAlpha : 1 , x : 0})
      .fromTo(btn,0.7,{ autoAlpha : 0, x : -50 } , { autoAlpha : 1 , x : 0})
  });
  return (
    <HomeWrapper ref={wrapper}>
      <h1>Konstytucja 3 Maja</h1>
      <button onClick={startFunc}>Dalej</button>
    </HomeWrapper>
  );
};

export default Home;
