import React from 'react';
import styled from 'styled-components';
import k3m from '../images/k3m.jpg';

const SectionWrapper = styled.div`
  width: 100vw;
  height: 50vh;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(${k3m});
  background-size: cover;
  background-attachment: fixed;

  h1 {
    font-size: 5vh;
    margin: 0;
    padding-left: 5vw;
    padding-top: 5vh;
  }
  h2 {
    margin: 0;
    font-size: 3vh;
    padding-top: 5vh;
    color : white;
    max-width : 85vw;
    padding-left : 5vw;
  }

  @media screen and (orientation: landscape) {
    h1 {
      font-size: 4vw;
      margin: 0;
      padding-top: 5vh;
    }
    h2 {
      margin: 0;
      font-size: 3vw;
      padding-top: 10vh;
      max-width : 90vw;
      padding-left : 5vw;
      text-align : center;
    }
  }
`;

const OtherSection = () => {
  return (
    <SectionWrapper>
      <h2>
        Władzę ustawodawczą miał stanowić dwuizbowy parlament składający się z
        Sejmu oraz Senatu. Funkcję wykonawczą miał stanowić król i odpowiedzialny
        przed Sejmem rząd
      </h2>
    </SectionWrapper>
  );
};

export default OtherSection;
