import React from 'react';
import styled from 'styled-components';
import image from '../images/i3.png';

const SectionWrapper = styled.div`
  width: 100vw;
  height: 60vh;
  background-image: url(${image});
  background-size: cover;

  h1 {
    font-size: 4vh;
    margin: 0;
    padding-left: 5vw;
    padding-top: 5vh;
  }
  h2 {
    margin: 0;
    font-size: 2.5vh;
    padding-left: 5vw;
    padding-top: 8vh;
    max-width: 40vh;
  }

  @media screen and (orientation: landscape) {
    h1 {
      font-size: 4vw;
      margin: 0;
      padding-left: 6vh;
      padding-top: 5vh;
    }
    h2 {
      margin: 0;
      font-size: 2vw;
      padding-left: 6vh;
      padding-top: 5vh;
      max-width: 50vw;
    }
  }
`;

const Section3 = () => {
  return (
    <SectionWrapper>
      <h2>
        Artykuł czwarty utrzymywał poddaństwo chłopów wobec szlachty, przyjmując
        jednak włościan "pod opiekę prawa i rządu krajowego". Ustanowiono
        trójpodziału władzy
      </h2>
    </SectionWrapper>
  );
};

export default Section3;
