import React from 'react';
import styled from 'styled-components';
import image from '../images/i2.png';

const SectionWrapper = styled.div`
  width: 100vw;
  height: 80vh;
  background-image: url(${image});
  background-size: cover;

  h1 {
    font-size: 4vh;
    margin: 0;
    padding-left : 5vw;
    padding-top : 5vh;
  }
  h2 {
    margin: 0;
    font-size: 2.5vh;
    padding-left : 5vw;
    padding-top : 5vh;
    max-width : 40vh;
  }

  @media screen and (orientation : landscape) {
    h1 {
    font-size: 4vw;
    margin: 0;
    padding-left : 6vh;
    padding-top : 5vh;
  }
  h2 {
    margin: 0;
    font-size: 2vw;
    padding-left : 6vh;
    padding-top : 5vh;
    max-width : 50vw;
  }
  }
`;

const Section1 = () => {
  return (
    <SectionWrapper id="section1">
      <h1>Informacje Ogólne</h1>
      <h2>
        Sejm Czteroletni po burzliwej debacie 3 maja 1791 r. przyjął przez
        aklamację ustawę rządową, która przeszła do historii jako Konstytucja 3
        Maja. Była drugą na świecie i pierwszą w Europie ustawą regulującą
        organizację władz, prawa i obowiązki obywateli. Jej celem było ratowanie
        Rzeczypospolitej po I rozbiorze, przeprowadzonym przez Prusy, Austrię i
        Rosję w 1772 r
      </h2>
    </SectionWrapper>
  );
};

export default Section1;
